import React, { useEffect } from "react"
import "./styles.css"
import { Box, Button, Chip, Avatar } from "@material-ui/core"
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles"
import { purple } from "@material-ui/core/colors"
import queryString from "query-string"
import shuffle from "lodash.shuffle"
import { RouteComponentProps } from "@reach/router"
import {
  difference,
  flatten,
  splitEvery,
  pipe,
  compose,
  toLower,
  sortBy,
  prop,
  pathOr,
} from "ramda"

const theme = createMuiTheme({
  palette: {
    primary: purple,
  },
})

type Person = string

type Pair = Person[]

interface Position {
  pair: Pair
  name: Person
  x: number
  y: number
  isLeft: boolean
}

const getPairs = splitEvery(2) as (people: Person[]) => Pair[]

const getShuffledPairs = pipe(
  shuffle as any,
  getPairs
) as (people: Person[]) => Pair[]

const getPeopleFromParams = (search: string = ""): Person[] => {
  const params = queryString.parse(search, {
    arrayFormat: "comma",
  }) as { people?: Person[] }

  return pathOr([], ["people"], params)
}

const App = (props: RouteComponentProps) => {
  const search = props.location && props.location.search
  const people = getPeopleFromParams(search)
  const [pairs, setPairs] = React.useState<Pair[]>(getPairs(people))

  const [fixedPairs, setFixedPairs] = React.useState<Pair[]>([])

  useEffect(() => {
    setPairs(getPairs(people))
  }, [search])

  const isFixed = (pair: Pair) => fixedPairs.includes(pair)

  const changePairs = () => {
    const nextPairs = fixedPairs.concat(
      getShuffledPairs(difference(people, flatten(fixedPairs)))
    )
    setPairs(nextPairs)
    window.history.pushState(
      { path: "/pairs" },
      "",
      `/pairs?people=${flatten(nextPairs).join(",")}`
    )
  }

  const positions: Position[] = positioned(pairs)

  const sortedPositions = sortBy(
    compose(
      toLower,
      prop("name")
    )
  )(positions)

  return (
    <ThemeProvider theme={theme}>
      <Box
        bgcolor="black"
        height="100vh"
        display="flex"
        flexDirection="column"
        alignItems="center"
        justifyContent="center"
      >
        <Box
          width="400px"
          height="400px"
          display="flex"
          flexDirection="column"
          alignItems="center"
          justifyContent="center"
        >
          <Box>
            <Button fullWidth onClick={changePairs} color="primary">
              shuffle
            </Button>
          </Box>
          <Box width="240px" height="300px" marginTop={3}>
            <Box>
              {sortedPositions.map(person => (
                <Box
                  style={{
                    transitionDuration: "0.5s",
                    transform: `translate(${person.y * 140}px, ${person.x *
                      45}px)`,
                  }}
                  width="100px"
                  bgcolor="white"
                  position="absolute"
                  key={person.name}
                  onClick={() => {
                    if (fixedPairs.includes(person.pair)) {
                      setFixedPairs(difference(fixedPairs, [person.pair]))
                    } else {
                      setFixedPairs(fixedPairs.concat([person.pair]))
                    }
                  }}
                >
                  <Box
                    position="absolute"
                    right={person.isLeft ? 0 : undefined}
                    clone
                  >
                    <Chip
                      color="primary"
                      avatar={
                        <Avatar
                          alt={person.name}
                          src={`https://github.com/${person.name}.png`}
                        />
                      }
                      label={person.name}
                      variant={isFixed(person.pair) ? "default" : "outlined"}
                    />
                  </Box>
                </Box>
              ))}
            </Box>
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default App

function positioned(pairs: Pair[]): Position[] {
  return pairs.reduce<Position[]>((acc, pair: Pair, rowIndex) => {
    return acc.concat(
      pair.map(
        (name: Person, colIndex): Position => ({
          pair,
          name,
          x: rowIndex,
          y: colIndex,
          isLeft: colIndex === 0,
        })
      )
    )
  }, [])
}
